.minapp{
    min-height: 100vh;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgb(121, 119, 119, 0.5));
}

.searchbox{
    margin: 0 50px 50px 50px;
   
}

.searchbox  .searchtext{
    width: 100%;
    display: block;
    outline: none;
    border: none;
    font-size: 26px;
    border-radius: 10px 10px 10px 10px;
    padding: 10px;
    color:darkblue;
    transition: 1s ease-in-out;
    box-shadow: navajowhite 0px 2px;
}

.searchbox  .searchtext :focus{
    background-color: darkcyan;
    box-shadow: navajowhite 0px 2px;
}

.location .locationname{
    color:midnightblue;
    font-size: 20px;
    text-align: center;
    text-shadow: 1px 1px floralwhite;
    font-weight: 900;
 
}

.location .locationdate{
    color:midnightblue;
    font-size: 13px;
    text-align: center;
    margin-top: 3px;
    font-style: italic;
    font-weight: 700;
    text-shadow: 1px 1px floralwhite;
}

.temperaturebox{
    text-align: center;
}

.temperaturebox .temperature{
    text-align: center;
    background-color: rgba(169, 164, 107, 0.442);
    display: inline-block;
    position: relative;
    margin-top: 10px;
    font-size: 60px;
    padding: 20px;
    border-radius: 10px;
    color:whitesmoke;
    font-weight: 900;
    box-shadow: 4px 4px wheat;
    text-shadow: 3px 3px grey;
}

.temperaturebox .temperaturetype{
    text-align: center;
    font-weight: 800;
    text-shadow: 1px 1px white;
}